import React from "react";
import { Descriptions, Radio, Button } from "antd";
import { useParams, useHistory } from "react-router-dom";
import products from "../assets/product.json";
import styled from "styled-components";
import AddToCart from "../components/AddToCart"
const Div = styled.div`
  display:flex;
  flex-wrap: nowrap;
  justify-content:center;
  margin:0 10px;
`;
const StyledDescriptions = styled(Descriptions)`
  display:flex;
  flex-wrap: nowrap;
  justify-content:center;
`;

function Component() {
  const params = useParams();
  const size = "small";
  var childrenItem;
  var type;
  for (var product in products) {
    if (product === params.type) {
      for (var list in products[product]) {
        type=product
        if(list === params.product)
        childrenItem=products[product][list]
      }
    }
  }
  return (
    <Div>
      <img
        alt="example"
        src={childrenItem.photoAddress}
        style={{width:500}}
      />
      <StyledDescriptions bordered size={size}>
        <Descriptions.Item label="Product">{childrenItem.name}</Descriptions.Item>
        <Descriptions.Item label="Amount">${childrenItem.price}</Descriptions.Item>
        <Descriptions.Item label="description">{childrenItem.description}</Descriptions.Item>
        <Descriptions.Item ><AddToCart No={childrenItem.No} type={type}>Add To Cart</AddToCart></Descriptions.Item>
      </StyledDescriptions>
    </Div>
  );
}

export default Component;
